* {
  margin: 0;
  padding: 0;
}

@keyframes fall {
  from {
  }
  to {
    margin-top: 100vh;
  }
}
@keyframes filling {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.01);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes full {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes empty {
  0% {
    transform: scale(1);
  }
  25% {
    transform: rotate(-5deg);
  }
  50% {
    transform: rotate(5deg);
  }
  75% {
    transform: rotate(-5deg);
  }
  100% {
    transform: scale(1);
  }
}

p {
  font-family: Arial, Helvetica, sans-serif;
}
